import React, { useState } from "react";
import ReactGA from "react-ga";
import { ISO3CountryCodes } from "./utils/utils";
import { Stats, Header, Footer, AllCountries } from "./components";
import "./App.css";
const trackingId = "UA-164329346-1";
ReactGA.initialize(trackingId);
ReactGA.pageview("/covid-tracker");

function App() {
  const [selectedCountryCode, setSelectedCountryCode] = useState("");
  const [toggleSection, setToggleSection] = useState(false);
  return (
    <>
      <Header />
      <div className="menu">
        <ul className="menu-ul">
          <li
            onClick={() => {
              ReactGA.event({
                category: "Clicks",
                action: !toggleSection
                  ? "Go to all countries stats"
                  : "Go to all global stats",
              });
              setToggleSection(!toggleSection);
            }}
          >
            {!toggleSection
              ? "Go to all countries stats"
              : "Go to all global stats"}
          </li>
        </ul>
      </div>
      {!toggleSection ? (
        <>
          <div className="stats-section">
            <h2 className="title">
              {selectedCountryCode
                ? ISO3CountryCodes[selectedCountryCode]
                : "Global"}
            </h2>
            <select
              id="country"
              className="select-css"
              onChange={(e) => {
                setSelectedCountryCode(e.target.value);
              }}
              defaultValue={selectedCountryCode}
            >
              <option value="">Select Country</option>
              {Object.entries(ISO3CountryCodes).map(([code, country]) => (
                <option key={code} value={code}>
                  {country}
                </option>
              ))}
            </select>
          </div>
          <div className="stats-cards">
            {selectedCountryCode ? (
              <Stats
                url={`https://corona.lmao.ninja/v2/countries/${selectedCountryCode}`}
              ></Stats>
            ) : (
              <Stats url={`https://corona.lmao.ninja/v2/all`}></Stats>
            )}
            {/* <CountryList /> */}
          </div>
          <Footer />
        </>
      ) : (
        <div className="all-countries">
          <AllCountries />
          <Footer />
        </div>
      )}
    </>
  );
}

export default App;
